import { useMediaQuery } from 'react-responsive';
import {
  ContributeSection,
  ContributeDescr,
  ContributeTitle,
  DonateStyledLink,
  LinksList,
  LinksListItem,
  OrderStyledLink,
  TextWrapper,
} from './Contribution.styled';

export const Contribution = () => {
  const isMobile = useMediaQuery({ query: '(max-width: 767px)' });

  return (
    <ContributeSection>
      <TextWrapper>
        {isMobile ? (
          <ContributeDescr>  </ContributeDescr>
        ) : (
          <ContributeTitle>
              <br />
              
          </ContributeTitle>
        )}

        {isMobile ? (
          <ContributeTitle>
              <br />
              
          </ContributeTitle>
        ) : (
          <ContributeDescr>  </ContributeDescr>
        )}
      </TextWrapper>

      <LinksList>
        <LinksListItem>
          <DonateStyledLink to="/#donation"></DonateStyledLink>
        </LinksListItem>
        <LinksListItem>
          <OrderStyledLink to="/order"> </OrderStyledLink>
        </LinksListItem>
      </LinksList>
    </ContributeSection>
  );
};
